import React, { createContext, useContext, useState } from "react";

const PPIContext = createContext();
export const usePpiBankTransfer = () => useContext(PPIContext);

export const BankTransferPPIProvider = ({ children }) => {
  const [globalLoader, setGlobalLoader] = useState(false);
  const [currentPage, setCurrentPage] = useState("ppi-status-page");
  const [beneficiaryList, setBeneficiaryList] = useState([]);

  return (
    <PPIContext.Provider
      value={{
        globalLoader,
        setGlobalLoader,
        currentPage,
        setCurrentPage,
        beneficiaryList,
        setBeneficiaryList,
      }}
    >
      {children}
    </PPIContext.Provider>
  );
};

export default BankTransferPPIProvider;
